import React from 'react';
import CodePanel from '../../Components/CodePanel';

export default () => {
  return (
    <>
      <section className="accent-bottom panel">
        <h2>Names scores</h2>
        <h4>Problem 22</h4>
        <p>
          Using <a href="/Projects/Project-Euler/P22.txt">names.txt</a> (right click and 'Save Link/Target As...'), a 46K text
          file containing over five-thousand first names, begin by sorting it into
          alphabetical order. Then working out the alphabetical value for each
          name, multiply this value by its alphabetical position in the list to
          obtain a name score.
        </p>
        <p>
          For example, when the list is sorted into alphabetical order, COLIN,
          which is worth 3 + 15 + 12 + 9 + 14 = 53, is the 938th name in the list.
          So, COLIN would obtain a score of 938 Ã— 53 = 49714.
        </p>
        <p>What is the total of all the name scores in the file?</p>
      </section>
      <CodePanel title="JavaScript">{`
        () => {
          const
          fs = require('fs'),    
          buffer = fs.readFileSync(process.cwd() + '/public/Projects/Project-Euler/P22.txt'),
          names = String(buffer).split(',').sort(),
          scores = {};

          for (let i = 0; i <= 90 - 65; i++) {
            scores[String.fromCharCode(65 + i)] = i + 1;
          }

          return names.reduce((total, name, index) => {
            return total + (index + 1) * name.slice(1, -1).split('').reduce((score, letter) => {
              return score + scores[letter];
            }, 0);
          }, 0);
        }
    `}</CodePanel>
    </>
  );
};